"use client"

import { useState, useMemo } from "react"
import { Search, X } from "lucide-react"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Checkbox } from "@/components/ui/checkbox"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Badge } from "@/components/ui/badge"
import { cn } from "@/lib/utils"
import type { CompetitionJudge } from "@/types/competition"

interface AssignJudgesModalProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  currentJudges: CompetitionJudge[]
  allJudges: CompetitionJudge[]
  onAssign: (judges: CompetitionJudge[]) => void
}

export function AssignJudgesModal({
  open,
  onOpenChange,
  currentJudges,
  allJudges,
  onAssign,
}: AssignJudgesModalProps) {
  const [searchQuery, setSearchQuery] = useState("")
  const [selectedJudgeIds, setSelectedJudgeIds] = useState<Set<string>>(
    new Set(currentJudges.map((j) => j.id))
  )

  const filteredJudges = useMemo(() => {
    if (!searchQuery.trim()) {
      return allJudges
    }
    const query = searchQuery.toLowerCase()
    return allJudges.filter(
      (judge) =>
        judge.fullName.toLowerCase().includes(query) ||
        judge.initials.toLowerCase().includes(query)
    )
  }, [allJudges, searchQuery])

  const selectedCount = selectedJudgeIds.size

  const handleToggleJudge = (judgeId: string) => {
    setSelectedJudgeIds((prev) => {
      const newSet = new Set(prev)
      if (newSet.has(judgeId)) {
        newSet.delete(judgeId)
      } else {
        newSet.add(judgeId)
      }
      return newSet
    })
  }

  const handleAssign = () => {
    const selectedJudges = allJudges.filter((judge) =>
      selectedJudgeIds.has(judge.id)
    )
    onAssign(selectedJudges)
    onOpenChange(false)
    setSearchQuery("")
  }

  const handleCancel = () => {
    setSelectedJudgeIds(new Set(currentJudges.map((j) => j.id)))
    setSearchQuery("")
    onOpenChange(false)
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[520px] max-h-[85vh] flex flex-col p-0" showCloseButton={false}>
        {/* Search - Full Width */}
        <div className="relative border-b border-border">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground pointer-events-none z-10" />
          <Input
            placeholder="Поиск по имени..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-9 h-10 w-full border-0 border-b border-border rounded-none bg-card"
          />
        </div>

        {/* Header */}
        <div className="px-6 pt-4 pb-4 border-b border-border">
          {selectedCount > 0 ? (
            <div className="flex items-center justify-between">
              <DialogTitle className="text-lg font-semibold">
                Выбрано судей: {selectedCount}
              </DialogTitle>
              <Button
                variant="ghost"
                size="sm"
                onClick={() => setSelectedJudgeIds(new Set())}
                className="h-8 px-3 text-xs font-medium text-muted-foreground hover:text-foreground"
              >
                Очистить все
              </Button>
            </div>
          ) : (
            <DialogTitle className="text-lg font-semibold">Назначить судей</DialogTitle>
          )}
        </div>

        {/* Judges List */}
        <div className="flex-1 min-h-0 px-6">
          <ScrollArea className="h-full">
            {filteredJudges.length === 0 ? (
              <div className="flex flex-col items-center justify-center py-16">
                <div className="flex items-center justify-center w-12 h-12 rounded-full bg-muted mb-3">
                  <Search className="h-5 w-5 text-muted-foreground" />
                </div>
                <p className="text-sm font-medium text-foreground mb-1">
                  {searchQuery ? "Судьи не найдены" : "Нет доступных судей"}
                </p>
                <p className="text-xs text-muted-foreground text-center max-w-[200px]">
                  {searchQuery
                    ? "Попробуйте изменить поисковый запрос"
                    : "Все доступные судьи уже назначены"}
                </p>
              </div>
            ) : (
              <div className="space-y-1 py-4">
                {filteredJudges.map((judge) => {
                  const isSelected = selectedJudgeIds.has(judge.id)
                  const isCurrent = currentJudges.some((j) => j.id === judge.id)

                  return (
                    <div
                      key={judge.id}
                      className={cn(
                        "flex items-center gap-3 px-3 py-2.5 rounded-md transition-colors cursor-pointer group",
                        isSelected && "bg-accent/50",
                        !isSelected && "hover:bg-accent/30"
                      )}
                      onClick={() => handleToggleJudge(judge.id)}
                    >
                      <Checkbox
                        checked={isSelected}
                        onCheckedChange={() => handleToggleJudge(judge.id)}
                        onClick={(e) => e.stopPropagation()}
                        className="flex-shrink-0"
                      />
                      <Avatar className="h-8 w-8 flex-shrink-0">
                        <AvatarFallback className="bg-muted text-muted-foreground text-xs font-medium">
                          {judge.initials}
                        </AvatarFallback>
                      </Avatar>
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center gap-2">
                          <p className={cn(
                            "text-sm leading-5",
                            isSelected ? "font-semibold text-foreground" : "font-medium text-foreground"
                          )}>
                            {judge.fullName}
                          </p>
                          {isCurrent && (
                            <Badge variant="outline" className="text-xs h-5 px-1.5">
                              Назначен
                            </Badge>
                          )}
                        </div>
                      </div>
                    </div>
                  )
                })}
              </div>
            )}
          </ScrollArea>
        </div>

        {/* Footer */}
        <DialogFooter className="px-6 py-4 border-t border-border gap-2">
          <Button variant="outline" onClick={handleCancel} className="h-9">
            Отмена
          </Button>
          <Button 
            onClick={handleAssign} 
            disabled={selectedCount === 0}
            className="h-9"
          >
            Добавить
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  )
}

